import Type from "../type";

const initialState = {
  login: false,
  token: false,
  JsonData:"",
  quotodata:[],
 
};

const Common = (state = initialState, action) => {
  console.log(action,"In Common");
  switch (action.type) {
    case Type.TOKEN:
    console.log(action.payload, "Token");
     return {
         ...state,
         token: action.payload
      }

    case Type.LOGIN:
      console.log(action.payload, "LOgin");

      return {
        ...state,
        login: action.payload,
      };
      case Type.JSONDATA:
        console.log(action.payload, "JSONDATA");
  
        return {
          ...state,
          JsonData: action.payload,
        };
        case Type.QUTATIONDATA:

          console.log(action.payload, "QUTATIONDATA");
          return { 
            ...state,
            quotodata:state.quotodata.concat(action.payload)
        }


    default:
      return state
  }
};

export default Common;
