import React, { useState } from "react";
import { Modal, Button } from "react-bootstrap";
import "../../../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import '../../cssFile/Modal.css'
import { useSelector } from "react-redux";



const ModalCompQutation = (props) => {
    const url=process.env.REACT_APP_SERVICE_ID
       const arr=useSelector((state)=>state.Common.quotodata)
       console.log(arr)
       
     console.log(arr)
      const onCloseFun=()=>{
        props.setShow(false);
      }

  return (
    <>


      <Modal
        show={props.show}
        size="xl"
        onHide={onCloseFun}
        aria-labelledby="ModalHeader"
        centered
        style={{height:"45rem"}}
      >
        <Modal.Header  closeButton onClick={onCloseFun}>
      
        </Modal.Header>
        <Modal.Body>
        <iframe src={`${url}getquote/${arr}`} style={{width: "-webkit-fill-available",
    height: "-webkit-fill-available"}}></iframe>
        </Modal.Body>
     
      </Modal>
    </>
  );
};

export default ModalCompQutation;
