import React, { useState } from "react";
import { Modal, Button } from "react-bootstrap";
import "../../../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../../cssFile/Modal.css";
import { useDispatch, useSelector } from "react-redux";
import ArrayUpdate from "../../../store/action/ArrayUpdate"
const ModalComp = (props) => {
  const url = process.env.REACT_APP_SERVICE_ID;
  const qutodata = useSelector((state) => state.Common.quotodata);
  console.log(qutodata);

  const dispatch = useDispatch();
  const AddQuoteFun = (e) => {
    e.preventDefault();
    // dispatch(ArrayUpdate( props.productId,));
    let s=props.productId 
    dispatch({
      type: "QUTATIONDATA",
      payload: s,
    });
    props.setShow(false)
  };

  return (
    <>
      <Modal show={props.show} aria-labelledby="ModalHeader" centered>
        <Modal.Header>
          <p1>Add to the quotation</p1>
          <Button
            variant="success"
            onClick={(e) => {
              AddQuoteFun(e);
            }}
          >
            Yes
          </Button>
          <Button variant="success" onClick={(e) => props.setShow(false)}>
            No
          </Button>
        </Modal.Header>
      </Modal>
    </>
  );
};

export default ModalComp;
