import React, { useEffect } from "react";
import classes from "../cssFile/Header.module.css";
import { Outlet, NavLink, useLocation, useNavigate } from "react-router-dom";
import axios from "axios";
import { useDispatch, useSelector } from "react-redux";
import menubar from "../Assets/menubar.png";
import Dropdown from "react-bootstrap/Dropdown";
const Header = (props) => {
  const location = useLocation();
  const userlogin = useSelector((state) => state.Common.token);
  const dispatch = useDispatch();
  const url = process.env.REACT_APP_SERVICE_ID;
  const nav = useNavigate();
  useEffect(() => {
    console.log("in HEader", userlogin);
  }, [userlogin]);

  const logoutAllFun = (e) => {
    e.preventDefault();
    console.log("IN Logout");
    axios.post(url + "user/logoutAll", { Authorization: `Bearer ${userlogin}` })
      .then((res) => {
        console.log(res);
        dispatch({
          type: "TOKEN",
          payload: false,
        });
        nav("/login");
      });
  };
  const logoutFun = (e) => {
    e.preventDefault();
    console.log("IN Logout");
    axios
      .post(url + "user/logout", { Authorization: `Bearer ${userlogin}` })
      .then((res) => {
        console.log(res);
        dispatch({
          type: "TOKEN",
          payload: false,
        });
        nav("/login");
      });
  };

  return (
    <>
      <header className={classes.header}>
        <h1>Aakash Metal Works</h1>

        <nav>
          <ul>
            <li>
              <NavLink
                className={location.pathname === "/" ? classes.activeClass : ""}
                to="/"
              >
                Home
              </NavLink>
            </li>
            <li>
              <NavLink
                className={
                  location.pathname === "/product" ? classes.activeClass : ""
                }
                to="/product"
              >
                Products
              </NavLink>
            </li>
            <li>
              <NavLink
                className={
                  location.pathname === "/about" ? classes.activeClass : ""
                }
                to="/about"
              >
                About
              </NavLink>
            </li>
            <li>
              <NavLink
                className={
                  location.pathname === "/contact" ? classes.activeClass : ""
                }
                to="/contact"
              >
                Contact
              </NavLink>
            </li>

            <li>
              <NavLink
                className={
                  location.pathname === "/faq" ? classes.activeClass : ""
                }
                to="/faq"
              >
                Knowledge Centre
              </NavLink>
            </li>

            {userlogin === false || userlogin === undefined ? (
              <li>
                <NavLink
                  className={
                    location.pathname === "/login" ? classes.activeClass : ""
                  }
                  to="/adminDashboard"
                >
                  Admin Page
                </NavLink>
              </li>
            ) : (
              <>
                <Dropdown>
                  <Dropdown.Toggle variant="success" id="dropdown-basic">
                    <img src={menubar} alt="menubar" />
                  </Dropdown.Toggle>

                  <Dropdown.Menu>
                    <Dropdown.Item
                      onClick={(e) => {
                        logoutFun(e);
                      }}
                    >
                      LogOut
                    </Dropdown.Item>
                    <Dropdown.Item onClick={(e) => {logoutAllFun(e)}}>LogOut All</Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
              </>
            )}
          </ul>
        </nav>
      </header>
    </>
  );
};

export default Header;

